@font-face {
  font-family: "Oswald Medium";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/oswaldmedium.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Oswald Regular";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswaldregular.woff2") format("woff2");
  font-display: swap;
}
html {
  height: 100%;
}
body {
  margin: 0;
  display: flex;
  flex-direction: column;
  min-height: 100%;
}
.main-container {
  flex-grow: 1;
}
a {
  text-decoration: none;
}
.page-wrapper {
  width: 280px;
  margin: 0 auto;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.reset-list {
  margin: 0;
  padding-left: 0;
  list-style-type: none;
}
.header {
  display: block;
  margin: 0;
  min-height: 65px;
  font-family: "Oswald Regular", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 40px;
  text-align: center;
  color: #000000;
}
.main-nav__logo-list {
  display: flex;
  justify-content: space-between;
  min-height: 66px;
}
.main-nav__menu-item {
  min-height: 66px;
}
.main-nav__link {
  font-family: "Oswald Medium", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
  text-align: center;
  color: #000000;
}
.banner {
  padding-top: 27px;
}
.banner__header {
  margin-bottom: 25px;
  line-height: 36px;
  color: #ffffff;
}
.banner-wrapper::before {
  position: absolute;
  top: 265px;
  left: 0;
  z-index: -1;
  display: block;
  content: "";
  width: 100%;
  height: 302px;
  background-color: #68b738d9;
}
.banner-wrapper::after {
  position: absolute;
  top: 265px;
  left: 0;
  z-index: -1;
  display: block;
  content: "";
  width: 100%;
  height: 302px;
  background-image: linear-gradient(#68b738d9, #68b738d9), url("../img/index/index-background-mobile.jpg");
  background-repeat: no-repeat;
  background-position: center;
}
.banner__text {
  margin-bottom: 29px;
  font-family: "Oswald Regular", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 14px;
  text-transform: uppercase;
  text-align: center;
  color: #ffffff;
}
.button-catalog {
  display: block;
  margin: 0;
  width: 100%;
  font-family: "Oswald Regular", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  text-align: center;
  text-transform: uppercase;
  color: #ffffff;
  background-color: #68b738;
  border: none;
}
.product-line__header {
  font-family: "Oswald Regular", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 37px;
  text-transform: uppercase;
  color: #000000;
}
.product-line__text {
  font-family: "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #444444;
}
.product-line__link {
  font-family: "Oswald Regular", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  text-align: center;
  text-transform: uppercase;
  color: #000000;
}
.product-line {
  background-color: #F2F2F2;
}
.features__text {
  font-family: "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #444444;
}
.demo {
  background-color: #eaeaea;
}
.demo__data-list {
  display: flex;
  flex-wrap: wrap;
}
.demo__text {
  font-family: "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #444444;
}
.data-top__description {
  font-family: "Oswald Regular", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 24px;
  color: #000000;
}
.data-top__term {
  font-family: "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 100%;
  color: #444444;
}
.data-bottom__term {
  font-family: "Oswald Regular", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  text-transform: uppercase;
  color: #000000;
}
.data-bottom__description {
  font-family: "Oswald Regular", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  text-transform: uppercase;
  color: #000000;
}
.data-top {
  display: flex;
  flex-direction: column-reverse;
}
.data-bottom {
  display: flex;
}
.data-bottom__description {
  margin: 0;
}
.slider__toggle {
  font-family: "Oswald Regular", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  color: #000000;
  border: none;
  background-color: transparent;
}
.page-footer--dark {
  background-color: #F2F2F2;
}
.page-footer__links {
  background-color: #F2F2F2;
}
.page-footer__logo {
  display: block;
  padding-top: 39px;
  padding-bottom: 32px;
  text-align: center;
}
.address__legend {
  font-family: "Oswald Regular", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  color: #111111;
}
.address__description {
  font-family: "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: #444444;
}
.address__map {
  position: relative;
  left: -20px;
}
.footer-social__list {
  display: flex;
  justify-content: center;
  padding: 22px 0;
}
